/*
  This boilerplate is based in ITCSS and SMACSS
*/
@import url("https://fonts.googleapis.com/css?family=PT+Sans");
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.6.3/css/font-awesome.css);
.samoyed-theme {
  --background-panel: url('../../img/background/bck-samoyed.jpg');
}

.shark-theme {
  --background-panel: url('../../img/background/aquarus.jpg');
}

.polar-theme {
  --background-panel: url('../../img/background/ .jpg');
}

.wolf-theme {
  --background-panel: url('../../img/background/bck-wolf.jpg');
}

.whale-theme {
  --background-panel: url('../../img/background/bck-medusa.jpg');
}

.galaxy-theme {
  --background-panel: url('../../img/background/bck-galaxy.jpg');
}

.phoenix-theme {
  --background-panel: url('../../img/background/bck-phoenix.jpg');
}

*,
*::before,
*:after {
  box-sizing: border-box;
}

* {
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  font-family: "sans-serif";
  background: var(--color-background-bck);
}

.contaier-all {
  width: 100%;
  height: 100%;
}

header {
  text-align: center;
}

h1 {
  color: var(--color-h1);
  font-size: 30px;
}

p {
  color: var(--color-text);
  font-size: 15px;
}

h2 {
  font-size: 30px;
  color: var(--color-h2);
}

.contaner-tnx {
  text-align: center;
}

.logo {
  width: 5em;
}

.hidden {
  display: none;
}

.center {
  text-align: center;
  justify-content: center;
}

.border-top {
  border-top: 2px solid black;
}

.not-detail {
  text-decoration: none;
  list-style: none;
}

.p-5 {
  padding: 5px;
}

.float-left {
  float: left;
}

.float-right {
  float: right;
}

.d-flex {
  display: flex;
}

.equalFlex {
  flex: 1;
}

.equalHMWrap {
  justify-content: space-between;
}

.equalHMRWrap {
  justify-content: space-between;
  flex-wrap: wrap;
}

.grid {
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: 1fr;
}

.content-center {
  width: 1000px;
  height: 90%;
  display: inline-flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media (max-width: 720px) {
  .content-center {
    width: 100%;
    height: 100%;
    flex-wrap: wrap;
  }
}

.content-center .panel-public {
  width: 55%;
  border-radius: 10px 0 0 10px;
}

@media (max-width: 720px) {
  .content-center .panel-public {
    display: none;
  }
}

@media (max-width: 720px) {
  .content-center .panel-public.panel-public-active {
    flex-wrap: wrap;
    display: inline-block;
    width: 100%;
  }
}

.content-center .panel-operations {
  position: relative;
  background: var(--background-panel);
  background-size: cover;
  width: 45%;
  border-radius: 0 10px 10px 0;
}

.content-center .panel-operations .btn-p-public {
  margin: 15px;
  display: none;
}

@media (max-width: 720px) {
  .content-center .panel-operations .btn-p-public {
    display: inline-block;
  }
}

@media (max-width: 720px) {
  .content-center .panel-operations {
    width: 100%;
  }
}

.content-center .panel-operations .picture-lg {
  position: absolute;
  right: 0;
  margin-right: -40px;
  margin-top: -20px;
}

@media (max-width: 720px) {
  .content-center .panel-operations .picture-lg {
    display: none;
  }
}

.content-center .panel-operations .content-tnx {
  position: relative;
  margin-top: 20px;
  margin: 5%;
  margin-top: 45%;
}

@media (max-height: 580px) {
  .content-center .panel-operations .content-tnx {
    margin-top: 35%;
  }
}

.content-center .panel-operations .content-tnx .container-login {
  border-radius: 10px;
}

.content-center .panel-operations .content-tnx .container-login form {
  margin-top: 20px;
}

.content-center .panel-operations .content-tnx .container-signup-active {
  animation-name: animation-in;
  animation-duration: 1s;
  padding-left: 0;
  border-radius: 10px;
  position: absolute;
  top: 40%;
  left: 50%;
  bottom: auto;
  transform: translate(-50%, -50%);
  margin-top: 10%;
  width: 90%;
}

@media (max-height: 580px) {
  .content-center .panel-operations .content-tnx .container-signup-active {
    top: 30%;
    animation-name: animation-in2;
  }
}

.content-center .panel-operations .content-tnx .container-signup-active #icon-open {
  float: right;
}

@keyframes animation-in {
  0% {
    top: 92%;
  }
  100% {
    top: 40%;
  }
}

@keyframes animation-in2 {
  0% {
    top: 92%;
  }
  100% {
    top: 30%;
  }
}

@keyframes animation-hidden {
  0% {
    top: 40%;
  }
  100% {
    top: 92%;
  }
}

.panel-public {
  overflow-y: auto;
  max-height: auto;
}

.masonry-layout {
  --columns: 1;
  --gap: 1rem;
  display: grid;
  grid-template-columns: repeat(var(--columns), 1fr);
  grid-gap: var(--gap);
}

@media (max-width: 600px) {
  .masonry-layout {
    --gap: 0.6rem;
  }
}

.masonry-layout .masonry-item {
  margin-bottom: var(--gap);
}

.masonry-layout.columns-1 {
  --columns: 1;
}

.masonry-layout.columns-2 {
  --columns: 2;
}

.masonry-layout.columns-3 {
  --columns: 3;
}

.masonry-layout.columns-4 {
  --columns: 4;
}

.masonry-layout.columns-5 {
  --columns: 5;
}

.masonry-layout.columns-6 {
  --columns: 6;
}

.btn-detail {
  font-size: 10px;
  font-weight: 600;
  text-transform: uppercase;
  padding: 2px 5px;
}

.details {
  background: var(--color-dark);
}

div.header-details div.title {
  color: var(--color-title);
  font-size: 35px;
  font-weight: 900;
  letter-spacing: 1.5px;
  padding: 35px 0 0 40px;
}

div.header-details div.desc {
  color: var(--color-text);
  font-size: 15px;
  font-weight: 400;
  letter-spacing: 1px;
  line-height: 20px;
  padding: 12px 40px 0 40px;
}

div.header-details .hosted-by {
  width: 128px;
  text-align: right;
  margin-top: 7px;
  padding: 7px 10px;
  background: var(--color-panel);
  border-radius: 7px;
  position: relative;
}

div.header-details .hosted-by img {
  height: 50px;
  border: 5px solid var(--color-border);
  border-radius: 100%;
}

div.header-details .hosted-by .tittle {
  position: absolute;
  font-size: 14px;
  font-weight: bold;
  right: 68px;
  color: var(--color-text);
  height: 100%;
  display: inline-block;
  top: 0;
  margin-top: 14px;
}

div.header-details .hosted-by .tittle .name {
  font-size: 10px;
  font-weight: 300;
  display: block;
}

div.body-details {
  padding: 52px 50px 0 40px;
}

div.body-details div.title {
  text-transform: uppercase;
  color: var(--color-title);
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 0.5px;
  transform: translateY(9px);
}

div.body-details .status ul.user-status {
  width: 100%;
  margin-top: 15px;
}

div.body-details .status ul.user-status li {
  list-style: none;
  display: inline-block;
  color: var(--color-text);
  padding: 15px 19px;
  text-transform: uppercase;
  font-size: 11px;
  font-weight: 900;
  letter-spacing: 0.7px;
  background: var(--color-panel);
}

div.body-details .status ul.user-status li:first-child {
  border-radius: 7px 0 0 7px;
}

div.body-details .status ul.user-status li:last-child {
  border-radius: 0 7px 7px 0;
}

div.body-details .status div.event-info {
  margin-top: 15px;
}

div.body-details .status div.event-info > span {
  background: var(--color-panel);
  border-radius: 7px;
  overflow: hidden;
}

div.body-details .status div.event-info .going-status ul li img {
  position: relative;
  width: 42px;
  border-radius: 100%;
  top: 50%;
  margin-top: -21px;
}

div.body-details .status div.event-info .going-status ul {
  padding: 7px;
  margin-bottom: 70px;
}

div.body-details .status div.event-info .going-status ul li {
  position: relative;
  float: left;
  list-style: none;
  width: 50px;
  height: 50px;
  background: var(--color-border);
  border-radius: 100%;
  text-align: center;
  box-sizing: border-box;
  z-index: 2;
}

div.body-details .status div.event-info .going-status ul li.li-more {
  line-height: 50px;
  color: var(--color-text);
  font-size: 22px;
  font-weight: 600;
  letter-spacing: 1px;
  background: rgba(0, 0, 0, 0.4);
}

div.body-details .status div.event-info .going-status .desc {
  color: var(--color-text);
  font-size: 15px;
  font-weight: 600;
  border-radius: 0 0 7px 7px;
}

div.body-details .status div.event-info .going-status .desc > span:first-child, div.body-details .status div.event-info .going-status .desc > span:last-child {
  width: 50%;
  display: inline-block;
  text-align: center;
}

div.body-details .status div.event-info .going-status .desc > span:last-child span.btn {
  background: rgba(0, 0, 0, 0.4);
}

.going-date .date {
  margin-top: 25px;
  height: 70%;
  grid-gap: 10px;
  font-size: 48px;
  font-weight: 900;
  background: var(--color-this-dark);
  color: var(--color-text);
  padding: 16.5px 8px;
}

.going-date .date .btn-detail {
  font-size: 12px;
  background: var(--color);
  color: var(--color-text);
}

.going-date .date .item:nth-child(1) {
  margin-top: 10px;
  grid-row: span 6;
}

.going-date .date .item:nth-child(n+2):nth-child(-n+3) {
  margin-top: 10px;
  grid-row: span 2;
}

* {
  font-family: 'PT Sans', sans-serif;
}

