:root {
  --fuschia:#ff0081;
  --color-light:#f5f5f5;
  --blue-light:#00497D;
  --color-text-btn:#f5f5f5;
  --cyan:#0ae;
  --purple:#332548;
  --color-dark: #282D31;
  --color-dark-alt: #141618;
  --color-panel-second:rgba(0,0,0,.5);
  --color-background-bck: #E2E9FF;
  --color-scroll:var(--cyan);
  --color-panel:var(--color-light);
  --color-active:		#9fdd62;
  --color-selected:#fff;
  --color-selection:#fff;
}

.c-c-details-dark {
  --color-title: rgba(255,255,255,0.8);
  --color-text:var(--color-light);
  --color-border:rgba(255,255,255,0.22);
  --color-panel:rgba(255,255,255,0.12);
  --color-this-dark: rgba(0,0,0,0.5);
