.panel-dark {
  --color-panel:var(--color-dark);
}

.c-c-second {
  --color-scroll:var(--color-dark );
}

.c-c-dark {
  --color-panel:var(--color-dark);
  --color-h2: #F2FAFF;
  --color-h1: var(--blue-light);
  --color-text: #fff;
  --color-icon: #F2FAFF;
  --color-text-opaque:  #E9E9E9;
