.c-c-fuschia {
  --color-icon:var(--fuschia);
  --color-button:var(--fuschia);
  --color-button-dark: #C10062;
  --btn-color-opaque:rgba(255, 0, 130, 0.5);
}

.c-c-white-opaque {
  --color: rgba(255,255,255,0.7);
  --color-hover: rgba(255,255,255,0.9);
}

.white {
  --color-panel:#fff;
  --color-h2: #fff;
  --color-h1: #fff;
  --color-text: #fff;
  --color-icon: #fff;
  --color-text-opaque:  #fff;
