  --color-gender:var(--color-light);
}

/*botones start*/
.button {
  background: var(--color-button);
  border: 2px solid var(--color-first);
  border-radius: 6px;
  padding: 5px 8px;
  text-align: center;
  -webkit-transition-duration: 0.4s;
  /* Safari */
  transition-duration: 0.4s;
  cursor: pointer;
  text-transform: uppercase;
  color: var(--color-light);
}

/*botones end*/
/*social*/
.social-container {
  text-align: center;
}

.social-icons {
  padding: 0;
  list-style: none;
  margin: 1em;
}

.social-icons li {
  display: inline-block;
  margin: 0.15em;
  position: relative;
